@import url('https://fonts.googleapis.com/css2?family=Orbitron&display=swap');

.wrapper {
  width: 375px;
  border-radius: 8px;
  font-family: 'Comic Sans MS';
  background-color: rgb(91, 175, 53);
}

h3 {
  background-color: rgb(0, 0, 0);
  color: rgb(91, 175, 53);
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  padding: 5px;
}

.comic {
  background-color: rgb(240, 238, 221);
  font-family: 'Comic Sans MS';
  border-radius: 8px;
  margin-left: 5px;
}

.input {
  margin-left: 10px;
  width: 75px;
}

.resultado1,
.resultado2 {
  font-family: 'Orbitron';
  font-size: 18px;
  font-weight: 600;
  height: 26px;
  background-color: rgb(162, 219, 139);
  color: rgb(3, 38, 104);
  margin-left: 5px;
  margin-right: 5px;
  padding: 5px;
}

.resultado1 {
  border-top-left-radius: 8px;
  border-top-right-radius: 8px;
  margin-top: 5px;
  margin-bottom: 0px;
  box-shadow: inset 2px 2px 2px rgba(0, 0, 0, 1);
  z-index: 1;
}

.resultado2 {
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  margin-top: 0px;
  box-shadow: inset 2px 0px 2px rgba(0, 0, 0, 1);
  z-index: 0;
}

.numpad-flex {  
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 5px;
  margin-left: 20%;
  margin-right: 20%;
  border-radius: 8px;
  background-color: rgb(162, 219, 139);
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 1);
}

.numpad {
  text-align: center;
  padding-top: 12%;
  padding-bottom: 12%;
}

.num-boton {
  font-family: 'Orbitron';
  font-size: 1rem;
  font-weight: bold;
  width: 2.8em;
  height: 2.8em;
  border: 2px solid black;
  border-radius: 7px;
  margin: 4px;
  box-shadow: 2px 2px 2px rgba(0, 100, 0, 1);
}

.num-boton:hover {
  background-color: green;
}

.num-boton:active {
  margin-left: 2px;
  margin-top: 2px;
  box-shadow: none;
}

#instrucciones {
  font-size: small;  
  margin-left: 5px;
}

.version {
  text-align: right;
  padding-right: 5px;
}